const sortedStatus = {
  value: "Sort Alphabetically",
  class: "unsorted",
  importance: "unsorted"
};

const RestauProvider = props => {
  const [restau, setRestau] = useState({ restauData, sortedStatus });
  return (
    <RestauContext.Provider value={[restau, setRestau]}>
      {props.children}
    </RestauContext.Provider>
  );
};

RestauProvider.propTypes = {
  children: PropTypes.node
};

export { RestauContext, RestauProvider };
